<template>
  <div class="bg animated fadeIn faster">
    <Login v-if="dom.login"/>
    <loading :active.sync="isLoading">
      <img alt="Meason" src="../../assets/img/loading.svg" width="100">
    </loading>
    <div class="modal-dialog modal-dialog-centered modal-lg" role="document" v-if="data">
      <div class="modal-content">
        <div class="modal-header">
          <h3 class="modal-title h5">{{data.title}}</h3>
          <router-link to="/partner" tag="button"
            type="button" class="close" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </router-link>
        </div>
        <div class="modal-body">
          <div class="modal-con">
            <div class="row m-0">
              <div class="col-md-6 mb-3">
                <div class="pic img-thumbnail" :style="{backgroundImage: `url('${data.picURL}')`}"></div>
              </div>
              <div class="col-md-6 info-con mb-3">
                <ul>
                  <li><b><i class="fas fa-map-marker-alt"></i> 地區:</b> {{data.city}}</li>
                  <li><b><i class="fas fa-guitar"></i> 誠徵:</b> <p><span v-for="i in data.need" :key="i" class="need">{{i}}</span></p></li>
                  <li><b><i class="fas fa-music"></i> 曲風:</b> {{data.type}}</li>
                  <li><b><i class="far fa-calendar-alt"></i> 日期:</b> {{data.date}}</li>
                </ul>
                <p class="contact" v-if="data.contact">
                  <span>聯絡方式</span>
                  {{data.contact}}
                </p>
              </div>
              <div class="col-md-12 bio">
                <ul class="nav nav-tabs" id="myTab" role="tablist">
                  <li class="nav-item">
                    <a class="nav-link active" id="band-tab" data-toggle="tab" href="#band" role="tab" aria-controls="band" aria-selected="true">
                      徵才說明 / 樂團介紹:
                    </a>
                  </li>
                  <li class="nav-item">
                    <a class="nav-link" id="comment-tab" data-toggle="tab" href="#comment" role="tab" aria-controls="comment" aria-selected="false">
                      留言板
                    </a>
                  </li>
                </ul>
                <div class="tab-content" id="myTabContent">
                  <div class="tab-pane fade show active" id="band" role="tabpanel" aria-labelledby="band-tab">
                    <pre class="pre" v-html="bioReplace" v-linkified>
                    </pre>
                  </div>
                  <div class="tab-pane fade" id="comment" role="tabpanel" aria-labelledby="comment-tab">
                    <div class="con">
                      <div class="item" v-for="(item, index) in data.comment" :key="index">
                        <router-link to="/" class="photo"
                          :style="{'backgroundImage':`url(${item.user.photoURL})`}"></router-link>
                        <div class="con">
                          <div class="text">
                            <router-link to="/">{{item.user.displayName}}</router-link>
                            <span>{{item.timestamp | dateBefore}}</span>
                            <pre class="mb-0">{{item.msg}}</pre>
                          </div>                        
                          <a class="editor" href="#" @click.prevent="" v-if="userData">
                            <i class="fas fa-ellipsis-v"></i>
                            <ul>
                              <li data-toggle="modal" data-target="#reportmodal-post"
                                @click.prevent="resetReportError('item.uid')"
                                v-if="item.user.uid !== userData.uid">檢舉</li>
                              <li v-if="item.user.uid === userData.uid">編輯</li>
                              <li v-if="item.user.uid === userData.uid">刪除</li>
                            </ul>
                          </a>
                        </div>
                        <div class="social" v-if="userData"
                          @click="liked(item)">
                          <span v-html="comLike(item)"></span>                        
                        </div>
                        <div class="social notLogin" v-else>
                          <span v-html="comLike_unLog(item)"></span>                        
                        </div>
                      </div>
                    </div>
                    <p v-if="!data.comment" class="no-comment">尚無留言</p>
                    <div class="write-comment" v-if="userData">
                      <div class="photo"
                        :style="{'backgroundImage': `url(${userData.photoURL})`}"></div>
                      <div class="text-are">
                        <div class="input-group mt-1">
                          <textarea class="form-control" placeholder="新增公開留言"
                            v-model="comments.text" name="comments"
                            id="comments"
                            :class="{'is-invalid': comments.error && !comments.text.trim()}"></textarea>
                          <div class="input-group-append">
                            <button class="btn btn-primary" type="button"
                              @click="postComment()">送出</button>
                          </div>
                        </div>
                        <p class="text-danger mt-1 small"
                          v-if="comments.error && !comments.text.trim()">
                          {{ comments.error }}
                        </p>
                      </div>
                    </div>
                    <div class="write-comment" v-else>
                      <div class="photo">未登入</div>
                      <div class="input-group mt-1">
                        <textarea class="form-control" placeholder="登入後即可留言" disabled></textarea>
                        <div class="input-group-append">
                          <button class="btn btn-primary" type="button"
                            @click="dom.login = true;dom.opened = false">
                            登入</button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>                
              </div>
          </div>
          </div>
        </div>
        <div class="modal-footer py-2">
          <router-link v-if="userData" class="btn btn-primary btn-lg px-5" type="button"
            tag="button" :to="`./${data.uid}/resume`">
            <span v-if="userData"><i class="fas fa-child"></i>我要加入</span>
          </router-link>
          <button v-else class="btn btn-primary btn-lg px-5" type="button"
            tag="button" @click="dom.login = true;dom.opened = false">
            <span><i class="fas fa-child"></i>我要加入</span>
          </button>
        </div>
      </div>
    </div>
    <router-view :userData="userData" :poster_email="data.poster_email" v-if="data" postType="樂團徵才"></router-view>
  </div>
</template>

<style lang="scss" scoped>
#comment{
  .item{
    display: flex;
    justify-content: space-between;
    &:nth-child(even) {
      background-color: #f6f6f6;
    }
    .photo{
      height: 50px;
      background-size: cover;
      border-radius: 50%;
      background-color: #f6f6f6;
      flex: 0 0 50px;
      border: 5px solid #fff;
      box-shadow: 0 0 1px #555;
      margin: 10px;
      display: block;
      background-position: center;
    }
    .con{
      flex: 1;
      display: flex;
      justify-content: space-between;
      padding: 10px 0;
      .text{
        span{
          color: #999;
          font-size: 14px;
          margin-left: 10px;
        }
        pre{
          white-space: normal;
        }
      }
    }
    .editor{
      display: none;
      padding: 5px 15px;
      color:#333;
      position: relative;
      align-self: center;
      color: 555px;
      svg{
        color: #999;
      }
      ul{
        width: 100px;
        transition: all 0.5s ease-in-out;
        position: absolute;
        border: 1px #999 solid;
        padding: 3px 20px;
        background-color: #fff;
        box-shadow: -1px -1px 6px rgba(0, 0, 0, 0.1);
        opacity: 0;
        z-index: -10;
        right: 0;
        bottom: 100%;
        margin-bottom: 0;
        box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
        list-style: none;
        li{
          transition: all 0.2s ease-in;
          &:hover{
            color: #fd8622;
          }
        }
      }
      &:focus{
        outline: none;
        color: #fd8622;
        ul{
          opacity: 1;
          color: #555;
          z-index: 100;
        }
      }
    }
    .social{
      display: flex;
      align-items: center;
      padding: 0 10px;
      margin: 10px 0;
      border-left: 1px solid #eee;
      color: #555;
      font-weight: bold;
      cursor: pointer;
      transition: color 0.2s ease-in;
      color: #fe9a3b;
      &:hover{
        color: darken(#fe9a3b, 10);
      }
      
      &:hover{
        color: #999;
      }
      &.notLogin{
        cursor: default;
        color: #888;
        &:hover {
          color: #888;
        }
      }
    }
    @media (max-width: 576px){
      flex-wrap: wrap;
      .con{
        padding-bottom: 0;
      }
      .photo{
        height: 30px;
        flex: 0 0 30px;
        border: 3px solid #fff;
        box-shadow: 0 0 1px #aaa;
      }
      .social{
        flex: 0 1 100%;
        margin: 0;
        border: none;
        margin-bottom: 10px;
      }
    }
    &:hover{
      .editor{
        display: block;
      }
    }
  }
  .no-comment{
    padding: 10px;
    color: #999;
    margin-bottom: 5px;
  }
  .write-comment{
    display: flex;
    .text-are{
      flex: 1
    }
    .photo{
      height: 50px;
      background-size: cover;
      border-radius: 50%;
      background-color: #f6f6f6;
      flex: 0 0 50px;
      border: 5px solid #fff;
      box-shadow: 0 0 1px #555;
      margin: 10px;
      background-image: url('../../assets/img/undefine.jpg');
      background-position: center;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      white-space: nowrap;
      font-weight: bold;
    }
    textarea{
      font-size: 14px;
      resize: none;
      &::-webkit-scrollbar {
        width: 0px;
      }
      &::-webkit-scrollbar-track {
        background: transparent
      }
      &::-webkit-scrollbar-thumb {
        background-color: transparent;
      }
      &::-webkit-scrollbar-thumb:hover {
        background-color: transparent;
      }
      &:focus{
        box-shadow: none;
      }
    }
    @media (max-width: 576px){
      .photo{
        height: 30px;
        flex: 0 0 30px;
        border: 3px solid #fff;
        box-shadow: 0 0 1px #aaa;
      }
    }
  }
}
</style>

<script>
import $ from 'jquery';
import firebase from 'firebase';
import Login from '../../components/Login.vue'

export default {
  props: ['userData'],
  data() {
    return {
      isLoading: false,
      data: null,
      location: window.location.href,
      dom:{
        login: false,
        opened: false,
      },
      comments: {
        text: '',
        error: '',
      },
      reports: {
        id: '',
        type: '',
        msg: '',
      }
    }
  },
  components:{
    Login
  },
  computed: {
    bioReplace() {
      return this.data.bio.replace(/\n/g,"<br>")
    }
  },
  methods: {    
    getData() {
      const vm = this;
      vm.isLoading = true;
      const url = `${process.env.VUE_APP_API}/getData`;
      let arr = [];
      vm.$http.post(url, {"url": `/partner/${vm.$route.params.id}`})
      .then((response) => {
        vm.data = response.data;
        vm.isLoading = false;
      })
      .then((response) => {
        if(vm.data.comment) {
          const obj = vm.data.comment;
          const keys = Object.keys(obj);
          vm.data.comment = keys.map(key => obj[key]).reverse();
          vm.comments.text = '';
        }
      })
      .then(() => {
        if (vm.data.comment) {
          const arr = vm.data.comment;
          arr.forEach(function(item, index, array){
            let likeobj = item.liked;
            let arr2 = []
            if(item.liked) {
              let likeobj = item.liked
              for (let i in item.liked){
                arr2.push(i)
              }
            }
            vm.data.comment[index].liked = arr2;
          });
        }
      })
    },
    report() {
      const vm = this;
      let str = `
        <h1>檢舉《找夥伴》留言內容通知</h1>
        <b>貼文編碼: ${vm.reports.id}</b>
        <p>舉報原因: ${vm.reports.type}</p>
        <p>理由及證據:<br> ${vm.reports.msg}</p>
        <hr>
        <h2>舉報人資訊</h2>
        <b>舉報人 email: ${vm.userData.email}</b>
        <p>舉報人暱稱: ${vm.userData.displayName}</p>
      `;
      this.$validator.validate().then((result) => {
        if (result) {
          // do stuff if not valid.
          vm.sendReportMail(str);
        } else {
          // console.log('欄位不完整')
        }
      });
    },
    sendReportMail(str) {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/report`;
      vm.isLoading = true;
      this.$http.post(url, {"str": str}).then((response) => {
        vm.isLoading = false;
      })
      .catch(function(error){
        console.error("錯誤",error);
      });
    },
    resetReportError(idid) {
      this.errors.clear();
      this.reports.id = idid;
      this.reports.type = '';
      this.reports.msg = '';
      // this.errors.remove(field);
    },
    postComment() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/creatComment`;
      const reqData = {
        timestamp: Date.now(),
        user: {
          displayName: vm.userData.displayName,
          uid: vm.userData.uid,
          photoURL: vm.userData.photoURL,
        },
        liked: [],
        msg: vm.comments.text,
        legal: true,
      }
      if(vm.comments.text.trim()){
        vm.isLoading = true;        
        this.$http.post(url, {
          "formData": reqData,
          "url": `/partner/${vm.data.uid}/comment/`,
        }).then((response) => {
          vm.isLoading = false;
          vm.getData();
        })
        .catch(function(error){
          console.error("錯誤",error);
        });
      } else {
        vm.comments.error = '留言不得為空';
      }
    },
    comLike(item) {
      const vm = this;
      const user = vm.userData.uid;
      let str = '';
      if(Array.isArray(item.liked)){
        if(item.liked.indexOf(user) !== -1){
          str = `
            <i class="fas fa-thumbs-up"></i>
            <span>${item.liked.length}</span>
          `
        } else {
          str = `
            <i class="far fa-thumbs-up"></i>
            <span>${item.liked.length}</span>
          `
        }
      } else {
        str = `
          <i class="far fa-thumbs-up"></i>
          <span>0</span>
        `
      }
      return str
    },
    comLike_unLog(item) {
      let str = '';
      if(Array.isArray(item.liked)){
        str = `
          <i class="far fa-thumbs-up"></i>
          <span>${item.liked.length}</span>
        `
      } else {
        str = `
          <i class="far fa-thumbs-up"></i>
          <span>0</span>
        `
      }
      return str
    },
    liked(comment) {
      // 按讚
      const vm = this;
      const user = vm.userData.uid;
      if(vm.userData) {
        if(comment.liked) {
          if(comment.liked.indexOf(user) !== -1) {
            vm.sendLikeAPI(comment, 'unlike')
          } else {
            vm.sendLikeAPI(comment, 'likes')
          }          
        } else {
          vm.sendLikeAPI(comment, 'likes')
        }
      }      
    },
    sendLikeAPI(comment, method) {
      // 按讚 API
      const vm = this;
      const url = `${process.env.VUE_APP_API}/liked`;
      vm.$http.post(url, {
        "userId": vm.userData.uid,
        "method": method,
        "url": `/partner/${vm.data.uid}/comment/${comment.uid}/liked`
      }).then((response) => {
        vm.getData();
      })
    },
    autogrow(textarea) {
      const adjustedHeight=textarea.clientHeight;
      adjustedHeight = Math.max(textarea.scrollHeight,adjustedHeight);
      if (adjustedHeight > textarea.clientHeight) {
        textarea.style.height=adjustedHeight+'px';
      }
    }
  },
  created() {
    const vm = this;
    this.getData();
    vm.$bus.$on('close:login',(val) => {
      vm.dom.login = val;
    })
  },
}
</script>
